






























































import BaseImage from '@/components/base/Image.vue';
import Scrollable from '@/components/Scrollable.vue';
import { MeetingItem, MeetingColumn, COLUMN_ITEMS_KEY } from '../../typings';

export default {
  components: {
    BaseImage,
    Scrollable,
  },
  data: () => ({
    COLUMN_ITEMS_KEY,
  }),
  props: {
    x: Boolean,
    y: {
      type: Boolean,
      default: true,
    },
    item: {
      type: Object as unknown as () => MeetingItem,
      required: true,
    },
    column: {
      type: Object as unknown as () => MeetingColumn,
      required: true,
    },
  },
  computed: {
    items(): MeetingItem[] {
      return this.column[COLUMN_ITEMS_KEY]!;
    },
  },
  model: {
    event: 'update:item',
    prop: 'item',
  },
  methods: {
    changeItem(item: MeetingItem) {
      this.$emit('update:item', item);
    },
  },
};

