import Handbag from '@/models/Handbag';
import Shoe from '@/models/Shoe';

export const COLUMN_ITEMS_KEY = 'itemInfos' as const;

export type MeetingItem<RelateInfo extends Shoe | Handbag = Shoe | Handbag> = {
  relateInfo: RelateInfo,
  name: string,
  id: string,
  coverImageUrl: string,
  bindRelateCatalogId: string
  bindRelateId: string
};

export type MeetingColumn = {
  creator: string,
  gmtCreateTime: number,
  gmtModifyTime: number,
  id: string,
  meetingId: string,
  name: string,
  sort: number,
  [COLUMN_ITEMS_KEY]?: MeetingItem[]
};

// meeting/meeting/info?meetingId=1339541644743868418

export enum MeetingStatus {
  READAY = 1,
  UNDERWAY,
  OVER
}

export enum MeetingType {
  MEETING = 1,
  SHOWROOM,
}

export type Meeting = {
  endTime: number,
  entryId: string
  id: string
  logo: string
  name: string
  startTime: number
  status: null | MeetingStatus
  themeImage: string
  type: MeetingType
  cooperationId:string
  backgroundImage:string
  creator: string
};
