


















































































































































































































































































































/* eslint-disable vue/no-unused-components */
/* eslint-disable camelcase */
import GestureFrame from '@/components/GestureFrame.vue';
import Scrollable from '@/components/Scrollable.vue';
import BaseImage from '@/components/base/Image.vue';
import AspectWidth from '@/components/AspectWidth.vue';
import { lf2p } from '@/filters';
import isQt from '@/utils/is-qt';
import {
  Album360_720Item,
  AlbumItem, AlbumType, CatalogFile,
} from '@/models/Catalog';
import {
  computed, defineComponent, reactive, ref, watch,
} from '@vue/composition-api';
import extract from '@4dst-saas/public-utils/dist/extract';
import axios from '@/api/axios';
import Paging from '@/models/Paging';
import exec from '@/utils/exec';
import open from '@/utils/open-window';
import useLoader from '@4dst-saas/public-utils/dist/composables/use-loader';
import { getFileRelateIdByCatalogId } from '@/loaders/catalog';
import {
  MeetingItem, MeetingColumn, COLUMN_ITEMS_KEY,
} from '../typings';
import AlbumPicker from './components/AlbumPicker.vue';
import ItemPicker from './components/ItemPicker.vue';
import DetailHeader from './components/Header.vue';
import ModelInfo from './components/ModelInfo.vue';
import AttachmentList from './components/AttachmentList.vue';

type GestureFrameOptions = { items: string[], value: [number, number], maxScale: number };
const IS_ALBUMN_UPDATED_KEY = Symbol('is albumn updated');

type Items = { url: string }[][];
function parseGestureFrameOptions(album: Album360_720Item) {
  const items: Items = [];
  album.imgArgument.list.forEach(item => {
    const indexs = item.index.split('_').map(i => parseInt(i, 10));
    items[indexs[0]] = items[indexs[0]] || [];
    items[indexs[0]][indexs[1]] = item;
  });
  return {
    items,
    value: album.imgArgument?.centerIndex.split('_').map(item => parseInt(item, 10)),
    maxScale: 3,
  };
}

export default defineComponent({
  props: {
    columnId: {
      type: String,
      required: true,
    },
    meetingId: {
      type: String,
      required: true,
    },
    itemId: {
      type: String,
      required: true,
    },
  },
  components: {
    AttachmentList,
    ModelInfo,
    DetailHeader,
    AlbumPicker,
    ItemPicker,
    AspectWidth,
    Scrollable,
    BaseImage,
    GestureFrame,
  },
  setup(props, { root }) {
    const meetingColumns = ref<MeetingColumn[]>();
    const currentColumn = ref<MeetingColumn>();
    const currentItem = ref<MeetingItem>();
    const itemPickerCanScrollDown = ref(true);
    const itemPickerCanScrollUp = ref(true);
    const columnPickerScrollData = ref<any>({});
    const currentModel = computed(() => {
      return currentItem.value?.relateInfo;
    });
    const currentAlbum = ref<AlbumItem>();
    const keyframe = ref<null | GestureFrameOptions>();
    const pictureWithSign = ref<string>();
    const isShowKeyframe = computed(() => {
      return currentAlbum.value?.imgType === AlbumType.IMG_360 || currentAlbum.value?.imgType === AlbumType.IMG_720;
    });
    const totalItems = computed(() => {
      return ([] as MeetingItem[]).concat(...(meetingColumns.value ?? []).map(column => (column[COLUMN_ITEMS_KEY] ?? [])));
    });
    const currentItemIndexOfTotal = computed(() => {
      if (!currentItem.value) { return undefined; }
      return totalItems.value.indexOf(currentItem.value);
    });

    function viewPicture({ target }: { target: Element }) {
      const imgList = currentItem.value?.relateInfo.imgList!;
      const index = imgList.indexOf(currentAlbum.value!);
      root.$ImageViewer.show(
        imgList.map(item => item.url!),
        {
          index,
          targetElems: [target],
        },
      );
    }
    function getNextColumnAndItem(prev = false) {
      const vMeetingColumns = meetingColumns.value!;
      const vCurrentColumn = currentColumn.value!;
      const vCurrentColumnItems = vCurrentColumn[COLUMN_ITEMS_KEY]!;
      const vCurrentItem = currentItem.value!;
      const itemIndex = vCurrentColumnItems.indexOf(vCurrentItem);
      const lastItemIndex = vCurrentColumnItems.length - 1;
      const columnIndex = vMeetingColumns.indexOf(vCurrentColumn);
      const lastColumnIndex = vMeetingColumns.length - 1;

      let targetColumn: MeetingColumn | undefined;
      let targetItem: MeetingItem | undefined;
      if (prev) {
        if (itemIndex === 0) {
          if (columnIndex) {
            targetColumn = vMeetingColumns[columnIndex - 1];
            const tmp = targetColumn[COLUMN_ITEMS_KEY]!;
            targetItem = tmp[tmp.length - 1];
          }
        } else {
          targetColumn = vCurrentColumn;
          const tmp = targetColumn[COLUMN_ITEMS_KEY]!;
          targetItem = tmp[itemIndex - 1];
        }
      } else if (itemIndex === lastItemIndex) {
        if (columnIndex !== lastColumnIndex) {
          targetColumn = vMeetingColumns[columnIndex + 1];
          const tmp = targetColumn[COLUMN_ITEMS_KEY]!;
          [targetItem] = tmp;
        }
      } else {
        targetColumn = vCurrentColumn;
        const tmp = targetColumn[COLUMN_ITEMS_KEY]!;
        targetItem = tmp[itemIndex + 1];
      }
      if (targetItem) {
        return {
          column: targetColumn,
          item: targetItem,
        };
      }
      return undefined;
    }

    const currentItemIndex = computed(() => {
      if (!currentItem.value) return undefined;
      return currentColumn.value?.[COLUMN_ITEMS_KEY]!.indexOf(currentItem.value);
    });

    function getNextItem(prev = false) {
      const vCurrentColumn = currentColumn.value;
      const idx = currentItemIndex.value;
      if (idx === undefined) return undefined;
      if (!vCurrentColumn) return undefined;
      const items = vCurrentColumn[COLUMN_ITEMS_KEY];
      if (!items) return undefined;
      if (prev) {
        if (currentItemIndex.value === 0) { return undefined; }
        return items[idx - 1];
      }
      if (currentItemIndex.value === items.length - 1) { return undefined; }
      return items[idx + 1];
    }

    const prevItem = computed(() => getNextItem());
    const nextItem = computed(() => getNextItem(true));

    const nextColumnAndItem = computed(() => getNextColumnAndItem());
    const prevColumnAndItem = computed(() => getNextColumnAndItem(true));
    function clickItem(column: MeetingColumn, item?: MeetingItem) {
      const targetItem = item ?? column[COLUMN_ITEMS_KEY]![column[COLUMN_ITEMS_KEY]!.length - 1];
      root.$router.replace({
        query: {
          columnId: column.id,
          meetingId: props.meetingId,
          itemId: targetItem.id,
        },
      });
    }

    function clickFocusModeNext(prev = false) {
      const s = prev
        ? prevColumnAndItem.value : nextColumnAndItem.value;
      if (s) {
        const { column, item } = s;
        if (item) {
          clickItem(column!, item);
        }
      }
    }

    async function handleOpenRenderer() {
      const vModel = currentModel.value;
      if (!vModel) return;
      let name = '';
      if ('styleName' in vModel && vModel.styleName) {
        name = vModel.styleName;
      } else {
        name = vModel.modelNumber ?? '';
      }
      const vCurrentItem = currentItem.value!;

      const catalogId = vCurrentItem.bindRelateCatalogId;
      const relateType = {
        SHOE: 0,
        HANDBAG: 3,
      }[vModel.UploadModelType as 'SHOE' | 'HANDBAG'];
      const relateId = await getFileRelateIdByCatalogId({
        catalogId,
        workspaceId: undefined,
        relateType,
      });
      const catalogOptions = {
        relateId,
        id: catalogId,
        name,
        relateType,
      };
      if (isQt()) {
        exec('openRenderer', catalogOptions);
      } else {
        open(`/model/${catalogOptions.relateId}?catalogId=${catalogOptions.id}&relateType=${catalogOptions.relateType}`);
      }
    }
    watch(currentAlbum, async (album) => {
      if (album && (album.imgType === AlbumType.IMG_NORMAL || album.imgType === AlbumType.IMG_CURRENT_MODEL)) {
        pictureWithSign.value = album.url;
      } else {
        pictureWithSign.value = undefined;
      }
    }, { immediate: true });
    const ensureGestureFrame = (vCurrentAlbum: AlbumItem) => {
      if (
        (vCurrentAlbum.imgType === AlbumType.IMG_360 || vCurrentAlbum.imgType === AlbumType.IMG_720)
        && !vCurrentAlbum.gestureFrame
      ) {
        vCurrentAlbum.gestureFrame = parseGestureFrameOptions(vCurrentAlbum);
      }
    };
    watch(
      () => extract(props, ['meetingId', 'columnId', 'itemId']),
      async (
        { columnId, meetingId, itemId }, old,
      ) => {
        const {
          columnId: oldColumnId,
          meetingId: oldMeetingId,
        } = old ?? { columnId: null, meetingId: null, itemId: null };
        let vMeetingColumns = meetingColumns.value;
        if (oldMeetingId !== meetingId || !vMeetingColumns) {
          vMeetingColumns = (await axios.post<Paging<MeetingColumn>>('meeting/conference/console', {
            meetingColumnPage: 1,
            meetingColumnSize: 10000,
            meetingId,
          })).data.records.filter(column => column[COLUMN_ITEMS_KEY] && column[COLUMN_ITEMS_KEY].length);
        }

        const vCurrentColumn = vMeetingColumns.find(item => item.id === columnId)!;
        if (oldColumnId !== columnId || !vCurrentColumn[COLUMN_ITEMS_KEY]) {
          vCurrentColumn[COLUMN_ITEMS_KEY] = (await axios.get<MeetingItem[]>('meeting/column/item/release/byColumn', {
            params: {
              meetingColumnId: columnId,
            },
          })).data;
        }
        const vCurrentItem = vCurrentColumn[COLUMN_ITEMS_KEY]?.find(item => item.id === itemId)!;
        // @ts-ignore
        if (!vCurrentItem[IS_ALBUMN_UPDATED_KEY]) {
          vCurrentItem.relateInfo.id = vCurrentItem.bindRelateId;
          vCurrentItem.relateInfo.imgList = (await axios.get<AlbumItem[]>('show/album/imgs', {
            params: {
              relateId: vCurrentItem.bindRelateCatalogId,
            },
          })).data;
          // 获取保存附件的系统目录id
          const filesParentId = (await axios.get<string>('/catalogue/yunpanCatalog/getSystemAppChildCountByParentId', {
            params: {
              parentId: vCurrentItem.bindRelateCatalogId,
              appType: 11002,
            },
          })).data;


          // 通过系统目录ID获取item
          vCurrentItem.relateInfo.binaryFileInfoList = (await axios.get<Paging<CatalogFile>>('bff-center/cloud-disk/list/getChildCatalogByPage', {
            params: {
              catalogId: filesParentId,
              cursor: 1,
              limit: 20,
            },
          })).data.records.map((item) => {
            return {
              ...item.fileInfo,
              name: item.name,
              downloadUrl: item.fileInfo.downloadPath,
            };
          });
        }


        const vCurrentAlbum = vCurrentItem.relateInfo?.imgList?.[0];
        // eslint-disable-next-line prefer-destructuring

        ensureGestureFrame(vCurrentAlbum);
        meetingColumns.value = vMeetingColumns;
        currentColumn.value = vCurrentColumn;
        currentItem.value = vCurrentItem;
        currentAlbum.value = vCurrentAlbum;
      },
      {
        immediate: true,
      },
    );
    const isDetailShown = ref(false);

    type LikeStatus = {
      userLoveCount: number,
      userLoveStatus: boolean,
    };
    const likeStatus = useLoader(async ({ currentItem: _currentItem }) => {
      if (!_currentItem) return undefined;
      try {
        return (await axios.post<LikeStatus>('meeting/userLoveStatistics/statisticsAndUserLoveStatus', {
          relateId: _currentItem.id,
          relateType: 0,
        })).data;
      } catch { }
      return undefined;
    }, reactive({ currentItem }));

    const toggleLike = async (like = !likeStatus.value?.userLoveStatus) => {
      const vCurrentItem = currentItem.value;
      const vLikeStatus = likeStatus.value;
      if (!vCurrentItem || !vLikeStatus) throw Error('还没加载完');
      if (like === vLikeStatus.userLoveStatus) return;
      const params = {
        relateId: vCurrentItem.id,
        relateType: 0,
      };
      if (like) {
        await axios.post('meeting/userLoveStatistics/addUserLoveStatus', params);
      } else {
        await axios.post('meeting/userLoveStatistics/cancelUserLoveStatus', params);
      }
      vLikeStatus.userLoveStatus = like;
      vLikeStatus.userLoveCount += like ? 1 : -1;
    };
    const columnPickerScrollable = ref();
    return {
      isDetailShown,
      toggleLike,
      toggleDetail() {
        isDetailShown.value = !isDetailShown.value;
      },
      changeAlbum(album: AlbumItem) {
        ensureGestureFrame(album);
        currentAlbum.value = album;
      },
      changeColumn(column: MeetingColumn) {
        root.$router.replace({
          query: {
            columnId: column.id,
            meetingId: props.meetingId,
            itemId: column[COLUMN_ITEMS_KEY]?.[0].id,
          },
        });
      },
      toggleFullscreen() {
        (root as any).toggleFullscreen();
      },
      clickFocusModeNext,
      changeItem(item: MeetingItem) {
        root.$router.replace({
          query: {
            columnId: props.columnId,
            meetingId: props.meetingId,
            itemId: item.id,
          },
        });
      },
      updateGestureValue(v) {
        console.log(currentAlbum.value.gestureFrame);
        currentAlbum.value.gestureFrame.value = v;
      },
      scrollColumnPicker() {
        const vColumnPickerScrollable = columnPickerScrollable.value;
        if (columnPickerScrollData.value.canScrollRight) {
          vColumnPickerScrollable.scroll();
        } else {
          vColumnPickerScrollable.scrollToStart();
        }
      },
      columnPickerScrollable,
      likeStatus,
      currentDetailPanel: ref('info'),
      totalItems,
      handleOpenRenderer,
      pictureWithSign,
      currentItemIndexOfTotal,
      prevItem,
      nextItem,
      nextColumnAndItem,
      prevColumnAndItem,
      COLUMN_ITEMS_KEY,
      columnPickerScrollData,
      itemPickerCanScrollUp,
      itemPickerCanScrollDown,
      keyframe,
      currentModel,
      isShowKeyframe,
      meetingColumns,
      currentColumn,
      currentItem,
      currentAlbum,
      viewPicture,
      lf2p,
      isQt,
    };
  },
});

