










import { MeetingItem, MeetingColumn } from '../../typings';

export default {
  props: {
    item: {
      type: Object as unknown as () => MeetingItem,
      required: true,
    },
    column: {
      type: Object as unknown as () => MeetingColumn,
      required: true,
    },
  },
  computed: {
    monetarySymbol(): string {
      const { relateInfo } = this.item;
      return { 1: '¥', 2: '$' }[relateInfo.priceUnit ?? relateInfo.monetaryUnit] ?? '';
    },
  },
};
