
import { computed, defineComponent } from '@vue/composition-api';
import getExt from '_/get-ext';
import { getTypeFormExt } from '@/utils/get-file-validation-options';
import downloadByURL from '@/utils/download-by-url';

const AttachmentList = defineComponent({
  name: 'AttachmentList',
  props: {
    attachmentList: {
      type: Array,
      default: () => ([]),
    },
  },
  setup(props) {
    const download = async (path, name) => {
      await downloadByURL(path, name);
    };

    const list = computed(() => {
      return props.attachmentList.map(attachment => {
        return {
          ...attachment,
          type: getTypeFormExt(getExt(attachment.downloadPath)),
        };
      });
    });

    const defaultImageSrc = (type) => {
      const UNKNOWN_ICON = require('@/assets/img/file-list/raw-file-icon/text.png');
      const vType = type;
      console.info(type);
      if (vType) {
        // eslint-disable-next-line import/no-dynamic-require
        return require(`@/assets/img/file-list/raw-file-icon/${vType}.png`);
      }
      return UNKNOWN_ICON;
    };
    return {
      list,
      download,
      defaultImageSrc,
    };
  },

  render() {
    const { list, download, defaultImageSrc } = this;
    return <ul class="file-list flex-cloumn">
      {list.map(attachment => {
        return <li class="flex-center item">
          <base-image
            defaultImage={defaultImageSrc(attachment.type)}
            src={['img', 'gif'].includes(attachment.type) ? attachment.downloadUrl : ''}
            fit="cover"
            class="file-item-image"
          />
          <span class="text-one-line flex-1" style="padding: 0 10px">{attachment.name}</span>
          <i class="i-xiazai" onclick={() => { download(attachment.downloadPath, attachment.name); }}></i>

        </li>;
      })}
    </ul>;
  },
});

export default AttachmentList;
