









































































































import Shoe from '@/models/Shoe';
import Handbag from '@/models/Handbag';
import shoeInfoSchema from '@/views/up-meeting/shoe/components/shoeInfoSchema';
import { i18n } from '@/i18n';
import { getDictByType } from '@/loaders/dictionary';
import TagsEditor from '@/components/base/TagsEditor.vue';

export default {
  components: {
    TagsEditor,
  },
  props: {
    model: {
      type: Object as unknown as () => Shoe | Handbag,
      required: true,
    },
  },
  data() {
    return {
      handbagTypeName: '',
    };
  },
  mounted() {
    if (this.model.relateType === 3) {
      (async () => {
        const handbagTypeObject = (await getDictByType({ type: 'handbagType' }));
        this.handbagTypeName = handbagTypeObject?.find(item => item.value === this.model.type)?.name;
      })();
    }
  },
  methods: {
    getGenderNameByValue(value) {
      if (!value) { return ''; }
      const genderSchema = shoeInfoSchema.find(field => field.prop === 'gender');
      if (genderSchema) {
        return genderSchema.oneOf.find(item => item.const.value === value)?.const.label;
      }
      return value;
    },
    getMonetaryUnitByValue(value) {
      const monetaryUnitDict = {
        1: i18n.language === 'en' ? 'CNY' : '人民币',
        2: i18n.language === 'en' ? 'USD' : '美元',
        CNY: i18n.language === 'en' ? 'CNY' : '人民币',
        USD: i18n.language === 'en' ? 'USD' : '美元',
      };
      return monetaryUnitDict[value];
    },
  },
};
