



































































import BaseImage from '@/components/base/Image.vue';
import Scrollable from '@/components/Scrollable.vue';
import { AlbumItem } from '@/models/Catalog';
import { MeetingItem } from '../../typings';

export default {
  components: {
    BaseImage,
    Scrollable,
  },
  props: {
    x: Boolean,
    y: {
      type: Boolean,
      default: true,
    },
    item: {
      type: Object as unknown as () => MeetingItem,
    },
    album: {
      type: Object as unknown as () => AlbumItem,
    },
  },
  model: {
    event: 'update:ablum',
    prop: 'album',
  },
  methods: {
    changeAlbum(album: AlbumItem) {
      this.$emit('update:album', album);
    },
  },
};

